<template>
  <div>
    <CRow class="justify-content-center">
      <CCol md="6">
        <CCard>
          <CCardHeader >
            <strong >Edit Thread </strong>
            
          </CCardHeader>
          <CCardBody>
            <CForm @submit.prevent="updateThread" method="post">
              <CInput
                label="Title thread"
                horizontal
                autocomplete="name"
                v-model="threadUpdate.thread_title"
                required
              />
              <CSelect
                label="Topik"
                horizontal
                :options="[ { value: 1, label: 'Parenting' }, { value: 2, label: 'Psikologi' }, { value: 3, label: 'Kesehatan' }, { value: 4, label: 'Tips' } ]"
                placeholder="Please select"
                :value.sync="threadUpdate.forum_topik_id"
              />
              <vue-editor
                class="mb-3"
                v-model="threadUpdate.thread_content"
                required
               />
              <CButton type="submit" size="sm" color="primary" class="mr-2"><CIcon name="cil-check-circle"/> save</CButton>
            </CForm>
          </CCardBody>
          
        </CCard>
        
      </CCol>
      
    </CRow>
  </div>
</template>

<script>
import {VueEditor} from "vue2-editor";
import axios from 'axios';

export default {
  name: 'EditThread',
  components: {
    VueEditor
  },
  data() {
    return {
      threadUpdate: {
        thread_title: '',
        thread_image: null,
        thread_content: '',
        forum_topik_id: 0,
      },
      threads: []
    }
  },
  methods: {
    setThread(data){
      this.threads = data;
    },
    updateThread(data){
      this.threads = data;

      let body = {
        admin_user_id : localStorage.getItem('admin_id'),
        thread_title: this.threadUpdate.thread_title,
        thread_image: this.threadUpdate.thread_image,
        thread_content: this.threadUpdate.thread_content,
        forum_topik_id: this.threadUpdate.forum_topik_id,
      };

      const result = 
        axios
        .post('http://103.151.215.74/api/admin/forum/thread/update/' + localStorage.getItem('threadId') , body, {
          headers: {
            Authorization: localStorage.getItem('token')
          }
        })
        .then(
          (result) => {
            // console.log("Berhasil : ", result);
            localStorage.removeItem('threadId');
            this.$router.push('/menus/forum');
          }
        )
        .catch(function (error) {
          // handle error
          // console.log("Gagal : ", error);
        });
    }
    
  },

  created() {
    let body = {
      admin_user_id : localStorage.getItem('admin_id')
    };

    axios
      .post('http://103.151.215.74/api/admin/forum/thread/edit/' + localStorage.getItem('threadId') , body, {
        headers: {
          Authorization: localStorage.getItem('token')
        }
      })
      .then(
        // (response) => this.setThread(response.data.json_data)
        (response) => {
          // console.log("Berhasil : ", response);
          this.setThread(response.data.json_data);
        }
         
      )
      // .catch(function (error) {
      //   // handle error
      //   // console.log("Gagal : ", error);
      // })
  },
}
</script>